.homePage {
  background: lightblue url(assets/pink-band-background.jpg) no-repeat center;
  background-attachment: fixed;
  background-size: cover; }

.title {
  font-size: 30px;
  margin-top: 0; }

.wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr; }
  @media (max-width: 480px) {
    .wrapper {
      grid-template-columns: 1fr;
      grid-template-rows: 1fr 1fr; } }

.coverContainer {
  display: grid; }
  .coverContainer .art {
    width: 450px;
    height: 450px;
    margin: auto;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out; }
    @media (max-width: 480px) {
      .coverContainer .art {
        width: 250px;
        height: 250px; } }
  .coverContainer .art:hover {
    -webkit-box-shadow: 0 0.5em 0.5em -0.4em var(--hover);
            box-shadow: 0 0.5em 0.5em -0.4em var(--hover);
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1); }
  .coverContainer .art:focus {
    -webkit-transform: translate(-0.25em);
        -ms-transform: translate(-0.25em);
            transform: translate(-0.25em); }

.logo {
  width: 6.5em; }
  @media (max-width: 480px) {
    .logo {
      width: 3em; } }



