.homePage {
  background: lightblue url(assets/pink-band-background.jpg) no-repeat fixed center;
  background-size: cover;
  height: 45em; }

.title {
  font-size: 30px;
  margin-top: 0; }

.coverContainer {
  display: grid; }
  .coverContainer .art {
    width: 450px;
    height: 450px;
    margin: auto;
    -webkit-transition: all 0.3s ease-in-out;
    -o-transition: all 0.3s ease-in-out;
    transition: all 0.3s ease-in-out; }
    @media (max-width: 480px) {
      .coverContainer .art {
        width: 300px;
        height: 300px; } }
  .coverContainer .art:hover {
    -webkit-box-shadow: 0 0.5em 0.5em -0.4em var(--hover);
            box-shadow: 0 0.5em 0.5em -0.4em var(--hover);
    -webkit-transform: scale(1.1);
        -ms-transform: scale(1.1);
            transform: scale(1.1); }
  .coverContainer .art:focus {
    -webkit-transform: translate(-0.25em);
        -ms-transform: translate(-0.25em);
            transform: translate(-0.25em); }

.logo {
  width: 6.5em; }



